const transactions = [
	{ userId: 1, amount: 50, date: "2025-04-01" },
	{ userId: 2, amount: 30, date: "2025-04-02" },
	{ userId: 1, amount: 100, date: "2025-04-15" },
	{ userId: 3, amount: 200, date: "2025-03-25" },
	{ userId: 2, amount: 150, date: "2025-03-29" },
	{ userId: 1, amount: 25, date: "2025-03-10" },
];

let obj = {};

function total(arr) {
	arr.forEach((item) => {
		obj[item.userId] = {
			totalSpent: 0,
			purchaseCount: 0,
			purchasesByMonth: {},
		};
		obj[item.userId].totalSpent = obj[item.userId].totalSpent + 1;
		obj[item.userId].purchaseCount =
			obj[item.userId].purchaseCount + item.amount;
		const itemDate = new Date(item.date);
		if (itemDate > new Date(obj[item.userId].lastPurchaseDate)) {
			obj[item.userId].lastPurchaseDate = item.date;
		}
		obj.push(obj[item.userId]);
	});
}

total(transactions);

